<template>
  <div class="hello">
    <br>
    <button class="btn blue darken-2"><router-link to="/form" class="white-text">Naar het formulier</router-link></button>
      <ul class="collection with-header">
        <li class="collection-header blue lighten-2"><h3 class="white-text">Meldingen</h3></li>
        <li v-for="problem in problems" :key="problem.id" class="collection-item">
          <h5><b>{{ problem.voornaam }}:</b></h5>
          <br />
          <h6>{{ problem.woningbouw }}</h6>
          <p>{{ problem.probleem }}</p>
        </li>
      </ul>
  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "hello",
  data() {
    return {
      problems: []
    };
  },
  created() {
    db
      .collection("woningbouw")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            voornaam: doc.data().voornaam,
            woningbouw: doc.data().woningbouw,
            probleem: doc.data().probleem
          };
          this.problems.push(data);
        });
      });
  }
};
</script>


<style scoped>
h3 {
  text-align: center;
}

ul {
  background-color: lightskyblue;
}
</style>



