<template>
  <main>
    <div id="new-problem">
      <h3>Klachten Formulier</h3>
      <div class="row">
        <form @submit.prevent="saveProblem" @keyup.enter="submit" class="col s12">
          <div class="row">
            <div class="input-field col s1"><i class="material-icons prefix">person</i></div>
            <div class="input-field col s11">
              <input type="text" v-model="voornaam" required>
              <label data-error="Vul uw voornaam in">Voornaam</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s1"><i class="material-icons prefix">person</i></div>
            <div class="input-field col s11">
              <input type="text" v-model="achternaam" required>
              <label>Achternaam</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s1"><i class="material-icons prefix">location_on</i></div>
            <div class="input-field col s11">
              <input type="text" v-model="adres" required>
              <label>Adres</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s1"><i class="material-icons prefix">local_phone</i></div>
            <div class="input-field col s11">
              <input type="tel" v-model="tel_nr" required>
              <label>Telefoonnummer</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s1"><i class="material-icons prefix">mode_edit</i></div>
            <div class="input-field col s11">
              <textarea v-model="probleem" class="materialize-textarea" required></textarea>
              <label>Probleem</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s1"><i class="material-icons prefix">home</i></div>
            <div class="input-field col s11">
              <input type="text" v-model="vereniging" required>
              <label>Bij welke woningbouw zit u?</label>
            </div>
          </div>
          <br />
          <button type="submit" class="btn blue lighten-2">Verzenden</button>
          <router-link to="/" class="btn grey">Annuleren</router-link>
        </form>
      </div>
    </div>
  </main>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "new-problem",
  data() {
    return {
      voornaam: null,
      achternaam: null,
      adres: null,
      tel_nr: null,
      probleem: null,
      vereniging: null,
      problem: []
    };
  },
  methods: {
    saveProblem() {
      db
        .collection("woningbouw")
        .add({
          voornaam: this.voornaam,
          achternaam: this.achternaam,
          adres: this.adres,
          tel_nr: this.adres,
          probleem: this.probleem,
          vereniging: this.vereniging
        })
        .then(
          docRef => this.$router.push("/"),
          (this.voornaam = ""),
          (this.achternaam = ""),
          (this.adres = ""),
          (this.tel_nr = ""),
          (this.probleem = ""),
          (this.vereniging = "")
        )
        .catch(error => console.log(er));
    }
  }
};
</script>

<style scoped>
html,
body {
  margin: 0;
}

hr {
  margin-top: 2em;
  border: solid 2px #000;
}

#form {
  margin-top: 2em;
}
</style>



